:local .top {
  height: 30%; }

:local .bottom {
  background-color: #f5f5f5;
  height: 70%;
  overflow: overlay; }

:local .video {
  height: 500px; }

@media (max-width: 576px) {
  :local .video {
    height: 200px !important; } }

@media (max-width: 1200px) {
  :local .bottom {
    height: auto !important;
    overflow: hidden; } }
